<template>
  <v-app class="grey lighten-3">
    <v-content>
      <v-container>
        <v-layout
          text-xs-center
          wrap
        >
          <v-flex mb-4>
            <h1 class="display-2 font-weight-bold mb-3">
              Portafolio de Inversión
            </h1>
            <p class="subheading font-weight-regular" >
              Utilizando Ethereum para Dapp Descentralizada
            </p>
            <p v-if="Connected && Sincronizar" class="subheading font-weight-regular" style="color: green">
              <strong>{{ account }}</strong>
            </p>
            <p v-else-if="Connected" class="subheading font-weight-regular" style="color: blue">
              <strong>
              Por favor, Sincronizar con Metamask de Ethereum</a>
              </strong>
            </p>
            <p v-else class="subheading font-weight-regular" style="color: red">
              <strong>
              Por favor, instalar <a href="https://metamask.io/">Metamask de Ethereum</a>
              </strong>
            </p>
            <p class="subheading font-weight-regular">{{resultado}}</p> 
          </v-flex>
        </v-layout>

        <v-layout v-if="Connected && Sincronizar" row justify-center >
          <v-dialog v-model="startProjectDialog" max-width="600px" persistent>
            <v-btn slot="activator" color="primary" dark>Nuevo Portafolio</v-btn>
            <v-card>
              <v-card-title >
                <span class="headline font-weight-bold mt-2 ml-4">
                  Nuevo Plan de Inversión
                </span>
              </v-card-title>
              <v-card-text class="pt-0">
                <v-container class="pt-0" grid-list-md>
                  <v-layout wrap>
                    <v-flex xs12>
                      <v-text-field
                        label="Título"
                        persistent-hint
                        v-model="newProject.title">
                      </v-text-field>
                    </v-flex>
                    <v-flex xs12>
                      <v-textarea
                        label="Descripción"
                        persistent-hint
                        v-model="newProject.description">
                      </v-textarea>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-text-field
                        label="Cantidad requerida (ETH)"
                        type="number"
                        step="0.0001"
                        min="0"
                        v-model="newProject.amountGoal">
                      </v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6>
                      <v-text-field
                        label="Duración (en días)"
                        type="number"
                        v-model="newProject.duration">
                      </v-text-field>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  flat
                  @click="startProjectDialog = false;
                  newProject.isLoading = false;">
                  Cerrar
                </v-btn>
                <v-btn color="blue darken-1"
                  flat
                  @click="startProject"
                  :loading="newProject.isLoading">
                  Guardar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </v-container>

      <v-container v-if="Connected && Sincronizar"
        grid-list-lg
      >
        <h1 class="display-1 font-weight-bold mb-3">
          Portafolios 
        </h1>       
        <v-layout  row wrap>
          <v-flex v-for="(project, index) in projectData" :key="index" xs12>
            <v-dialog
              v-model="project.dialog"
              width="800"
            >
              <v-card>
                <v-card-title class="headline font-weight-bold">
                  {{ project.projectTitle }}
                </v-card-title>
                <v-card-text>
                  {{ project.projectDesc }}
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="blue darken-1"
                    flat="flat"
                    @click="projectData[index].dialog = false"
                  >
                    Close
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 10 : 2}`"
              >
                <v-card-title primary-title>
                  <div>
                    <div class="headline font-weight-bold">
                      <v-chip
                        label
                        :color="stateMap[project.currentState].color"
                        text-color="white" class="mt-0">
                      {{ stateMap[project.currentState].text }}
                      </v-chip>
                      {{ project.projectTitle }}
                    </div>
                    <br/>
                    <span>{{ project.projectDesc.substring(0, 100) }}</span>
                    <span v-if="project.projectDesc.length > 100">
                      ... <a @click="projectData[index].dialog = true">[Show full]</a>
                    </span>
                    <br/><br/>
                    <small>Fecha tope: <b>{{ new Date(project.deadline * 1000) }}</b></small>
                    <br/><br/>
                    <small>Objectivo: <b>{{ weiEth(project.goalAmount) }} ETH </b></small>
                    <small v-if="project.currentState == 1">no se logró la meta</small>
                    <small v-if="project.currentState == 2">Fondo en Inversión</small>
                  </div>
                </v-card-title>
                <v-flex
                  v-if="project.currentState == 0 && account != project.projectStarter"
                  class="d-flex ml-3" xs12 sm6 md3>
                  <v-text-field
                    label="Cantidad (ETH)"
                    type="number"
                    step="0.0001"
                    min="0"
                    v-model="project.fundAmount"
                  ></v-text-field>
                  <v-btn
                    class="mt-3"
                    color="light-blue darken-1 white--text"
                    @click="fundProject(index)"
                    :loading="project.isLoading"
                  >
                    Invertir
                  </v-btn>
                </v-flex>
                <v-flex class="d-flex ml-3" xs12 sm6 md3>
                  <v-btn
                    class="mt-3"
                    color="amber darken-1 white--text"
                    v-if="project.currentState == 1"
                    @click="getRefund(index)"
                    :loading="project.isLoading"
                  >
                    Reembolso
                  </v-btn>
                  <v-btn
                    class="mt-3"
                    color="green darken-1 white--text"
                    v-if="project.currentState == 2"
                    @click="getRefund(index)"
                    :loading="project.isLoading"
                  >
                    Ganancias
                  </v-btn>
                </v-flex>
                <v-card-actions v-if="project.currentState == 0" class="text-xs-center">
                  <span class="font-weight-bold" style="width: 200px;">
                    {{ weiEth(project.currentAmount) }} ETH
                  </span>
                  <v-progress-linear
                    height="10"
                    :color="stateMap[project.currentState].color"
                    :value="(project.currentAmount/project.goalAmount)* 100"
                  ></v-progress-linear>
                  <span class="font-weight-bold" style="width: 200px;">
                    {{ weiEth(project.goalAmount) }} ETH
                  </span>
                </v-card-actions>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>

import Portafolio from './contracts/portafolio';
import Inversiones from './contracts/inversiones';
import ethers from 'ethers';
const provider = new ethers.providers.Web3Provider(window.ethereum);
const signer = provider.getSigner();

export default {
  name: 'App',
  data() {
    return {
      Connected: ethereum.isConnected(),
      Sincronizar: false,
      networkVersion: null,
      startProjectDialog: false,
      account: null,
      resultado: null,
      stateMap: [
        { color: 'primary', text: 'En Proceso' },
        { color: 'warning', text: 'Expiró' },
        { color: 'success', text: 'Completo' },
      ],
      projectData: [],
      timer: '',
      newProject: { isLoading: false }
    };
  },
  created () {
    this.Actualizar();
    this.timer = setInterval(this.Actualizar, 5000)
  },
  mounted() {
    this.getIni();
    this.getPortafolio();
    
  },

  methods: {

    Actualizar(value) {
      if (!this.Sincronizar ) {
        this.getIni();
      } 
    },

    getIni() {
      this.Sincronizar = false;
      ethereum.enable();
      ethereum.on('chainChanged', handleChainChanged);
      function handleChainChanged(_chainId) {
        window.loction.reload();
      } 
      signer.getAddress().then((result) => {
        this.account = result; 
        if (result.length > 0) {
          this.Sincronizar = true;    
        }              
      });
      this.networkVersion = ethereum.networkVersion; 
    },
    
    ethWei(value) {
        return 10**18 * value;
    },

    weiEth(value) {
        return value / 10**18;
    },

    getPortafolio() {
      this.projectData = [];
      Portafolio.returnAllProjects().then((projects) => {
        projects.forEach((projectAddress) => {
          const projectInst = Inversiones(projectAddress);
          projectInst.getDetails().then((inver) => {
            const projectInfo = inver;
            this.projectData.push({
              direccion:      inver[0],
              projectTitle:   inver[1],
              projectDesc:    inver[2],
              deadline:       inver[3],
              currentState:   inver[4],
              currentAmount:  inver[5],
              goalAmount:     inver[6],
              isLoading:      false,
              contract:       projectAddress
            });
           });
        });
      }); 
    },

    startProject() {
      this.newProject.isLoading = true;
      Portafolio.startProject(
        this.newProject.title,
        this.newProject.description,
        this.newProject.duration,
        this.ethWei(this.newProject.amountGoal)        
      );
      this.newProject.isLoading = false;
      this.startProjectDialog = false;
    },

    fundProject(index) {
      if (!this.projectData[index].fundAmount) {
        return;
      }
      if (this.account==this.projectData[index].direccion) {
        return;
      }
      const projectContract = Inversiones(this.projectData[index].contract);
      this.projectData[index].isLoading = true;
    
      projectContract.contribute({
        from:   this.account,
        value:  this.ethWei(this.projectData[index].fundAmount),
        gasLimit: 500000
      });
    
      this.projectData[index].isLoading = false;
      this.projectData[index].fundAmount = null;
    },
    getRefund(index) {
      /*
      this.projectData[index].isLoading = true;
      this.projectData[index].contract.methods.getRefund().send({
        from: this.account,
      }).then(() => {
        this.projectData[index].isLoading = false;
      });
      */
    },
  },
};
</script>
